import React, { useState, useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { Menu, X } from 'lucide-react';
import logo from '../assets/logo.svg';

const Navbar = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);
  const [searchValue, setSearchValue] = useState('');
  const [searchActive, setSearchActive] = useState(false);
  const location = useLocation();

  useEffect(() => {
    const handleScroll = () => {
      setScrolled(window.scrollY > 20);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const handleHomeClick = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
    if (isOpen) {
      setIsOpen(false);
    }
  };

  const scrollToSection = (selector) => {
    const target = document.querySelector(selector);
    if (target) {
      target.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
    if (isOpen) {
      setIsOpen(false);
    }
  };

  const handleModelClick = () => scrollToSection('#process');
  const handleConsultingClick = () => scrollToSection('#consulting');
  const handleServicesClick = () => scrollToSection('#services');
  const handlePortfolioClick = () => scrollToSection('#portfolio');
  const handleContactClick = () => scrollToSection('#contact');

  const suggestionItems = [
    { label: 'Home', action: handleHomeClick },
    { label: 'Our Model', action: handleModelClick },
    { label: 'Services', action: handleServicesClick },
    { label: 'Portfolio', action: handlePortfolioClick },
    { label: 'Consulting', action: handleConsultingClick },
    { label: 'Contact', action: handleContactClick }
  ];

  const filteredSuggestions = suggestionItems.filter((item) =>
    item.label.toLowerCase().includes(searchValue.trim().toLowerCase())
  );
  const showSuggestions =
    (searchActive || searchValue.trim().length > 0) && filteredSuggestions.length > 0;

  const handleSuggestionSelect = (action) => {
    if (typeof action === 'function') {
      action();
    }
    setSearchValue('');
    setSearchActive(false);
  };

  const renderSearchInput = (isMobile = false) => {
    const containerClass = isMobile ? 'relative px-2' : 'relative';
    const inputClass = isMobile
      ? 'mt-2 block w-full rounded-lg border border-slate-200 px-3 py-2 text-sm text-slate-600 placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500'
      : 'w-48 rounded-full border border-slate-200 bg-white/90 px-4 py-2 text-sm text-slate-600 placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500';
    const inputId = isMobile ? 'navbar-search-mobile' : 'navbar-search-desktop';
    const dropdownClass = `absolute left-0 right-0 mt-2 rounded-xl border border-slate-200 bg-white/95 shadow-lg overflow-hidden ${
      isMobile ? '' : ''
    }`;

    return (
      <div className={containerClass}>
        <label className="sr-only" htmlFor={inputId}>
          Search
        </label>
        <input
          id={inputId}
          type="search"
          placeholder="Search..."
          value={searchValue}
          onChange={(event) => setSearchValue(event.target.value)}
          onFocus={() => setSearchActive(true)}
          onBlur={() => setTimeout(() => setSearchActive(false), 120)}
          className={inputClass}
          aria-controls={`${inputId}-suggestions`}
        />
        {showSuggestions && (
          <ul
            id={`${inputId}-suggestions`}
            className={dropdownClass}
          >
            {filteredSuggestions.map((item) => (
              <li key={item.label}>
                <button
                  type="button"
                  className="w-full px-4 py-2 text-left text-sm text-slate-600 hover:bg-slate-50"
                  onMouseDown={(event) => {
                    event.preventDefault();
                    handleSuggestionSelect(item.action);
                  }}
                  onTouchStart={(event) => {
                    event.preventDefault();
                    handleSuggestionSelect(item.action);
                  }}
                >
                  {item.label}
                </button>
              </li>
            ))}
          </ul>
        )}
      </div>
    );
  };

  

  return (
    <nav className={`fixed w-full z-[100] transition-all duration-300 ${
      scrolled ? 'bg-white shadow-lg' : 'bg-white/95 backdrop-blur-sm'
    }`}>
      <div className="max-w-[1320px] mx-auto px-4 sm:px-6 lg:px-10">
        <div className="flex justify-between items-center h-20">
          {/* Logo */}
          <Link to="/" onClick={handleHomeClick} className="flex items-center space-x-3">
            <img src={logo} alt="EngiNerds" className="h-14 w-auto" />
            <span className="sr-only">EngiNerds</span>
          </Link>

          {/* Desktop Navigation */}
          <div className="hidden lg:flex items-center space-x-6">
            <Link
              to="/"
              onClick={handleHomeClick}
              className={`text-sm font-medium transition-colors duration-200 ${
                location.pathname === '/'
                  ? 'text-primary-500'
                  : 'text-slate-600 hover:text-primary-500'
              }`}
            >
              Home
            </Link>
            <button
              type="button"
              onClick={handleModelClick}
              className="text-sm font-medium text-slate-600 hover:text-primary-500 transition-colors duration-200"
            >
              Our Model
            </button>
            <a
              href="#services"
              className="text-sm font-medium text-slate-600 hover:text-primary-500 transition-colors duration-200"
            >
              Services
            </a>
            <a
              href="#portfolio"
              className="text-sm font-medium text-slate-600 hover:text-primary-500 transition-colors duration-200"
            >
              Portfolio
            </a>
            <button
              type="button"
              onClick={handleConsultingClick}
              className="text-sm font-medium text-slate-600 hover:text-primary-500 transition-colors duration-200"
            >
              Consulting
            </button>
            {renderSearchInput(false)}
            <button
              type="button"
              onClick={handleContactClick}
              className="bg-primary-500 text-white px-6 py-2.5 rounded-lg font-medium hover:bg-primary-600 transition-colors duration-200"
            >
              Contact Us
            </button>
          </div>

          {/* Mobile menu button */}
          <button
            onClick={() => setIsOpen(!isOpen)}
            className="lg:hidden p-2 rounded-lg text-slate-600 hover:bg-gray-100"
          >
            {isOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>
      </div>

      {/* Mobile Navigation */}
      {isOpen && (
        <div className="lg:hidden bg-white border-t">
          <div className="px-4 py-4 space-y-3">
            <Link to="/" onClick={handleHomeClick} className="block px-4 py-2 rounded-lg text-sm font-medium text-slate-600 hover:bg-gray-50">
              Home
            </Link>
            <button
              type="button"
              onClick={handleModelClick}
              className="block w-full text-left px-4 py-2 rounded-lg text-sm font-medium text-slate-600 hover:bg-gray-50"
            >
              Our Model
            </button>
            <a href="#services" onClick={() => setIsOpen(false)} className="block px-4 py-2 rounded-lg text-sm font-medium text-slate-600 hover:bg-gray-50">
              Services
            </a>
            <a href="#portfolio" onClick={() => setIsOpen(false)} className="block px-4 py-2 rounded-lg text-sm font-medium text-slate-600 hover:bg-gray-50">
              Portfolio
            </a>
            <button
              type="button"
              onClick={handleConsultingClick}
              className="block w-full text-left px-4 py-2 rounded-lg text-sm font-medium text-slate-600 hover:bg-gray-50"
            >
              Consulting
            </button>
            {renderSearchInput(true)}
            <button
              type="button"
              onClick={handleContactClick}
              className="block w-full bg-primary-500 text-white px-4 py-2.5 rounded-lg font-medium text-center hover:bg-primary-600"
            >
              Contact Us
            </button>
          </div>
        </div>
      )}
    </nav>
  );
};

export default Navbar;
